import React, {useState, useEffect} from "react";
import Typography from "@material-ui/core/Typography";
import {makeStyles} from '@material-ui/core/styles';
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import {Link, Route, useRouteMatch, useParams} from "react-router-dom";
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import axios from "axios"
import IconButton from "@material-ui/core/IconButton";
import InfoIcon from '@material-ui/icons/Info';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import clsx from 'clsx';
import Avatar from "@material-ui/core/Avatar";
import Collapse from "@material-ui/core/Collapse";
import CardHeader from "@material-ui/core/CardHeader";
import CircularProgress from "@material-ui/core/CircularProgress";


const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        margin: 10,
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    media: {
        height: 250,
    },
    link: {
        textDecoration: "none",
    }
}));


const HikeDescription = (props) => {

    const match = useRouteMatch('/hikeDescription/:hikeId');

    const [hikeDescription, setHikeDescription] = useState([]);
    const [loading, setLoading] = useState(false);

    const getHikeDescription = async () => {
        const data = await axios
            .get(`https://stormy-escarpment-89406.herokuapp.com/hikes/getHike?hike_id=${match.params.hikeId}`)
            .then(response => {
                setHikeDescription(response.data);
            })
        setLoading(true);
    }

    useEffect(() => {
        getHikeDescription();
    }, []);
    const classes = useStyles();

    const [expanded, setExpanded] = React.useState(false);

    const handleExpandClick = () => {
        setExpanded(!expanded);
    };
    return (

        <div>
            {
                loading ?

                    <Grid
                        container
                        direction="row"
                        justify="center"
                        alignItems="flex-start"
                        spacing={5}
                    >
                        <Grid item xs={12} md={12} lg={12}>
                            <Card className={classes.root}>
                                <CardHeader
                                    title={`Поход на ${hikeDescription.placeName}`}
                                    subheader={`Организатор: ${hikeDescription.groupName}`}
                                />
                                <CardMedia
                                    className={classes.media}
                                    image={hikeDescription.groupPhoto}
                                    title={hikeDescription.groupName}
                                />
                                <CardContent>
                                    <Typography variant="body2" color="textSecondary" component="p">
                                        {hikeDescription.hikeDescription}
                                    </Typography>
                                </CardContent>
                                <CardActions disableSpacing>
                                    <Link to={`/placeDescription/${hikeDescription.placeName}`}>
                                        <IconButton aria-label="about place">
                                            <InfoIcon color='primary'/>
                                        </IconButton>
                                    </Link>
                                    <IconButton aria-label="add to my hikes">
                                        <AddCircleIcon color='primary'/>
                                    </IconButton>
                                    <IconButton
                                        className={clsx(classes.expand, {
                                            [classes.expandOpen]: expanded,
                                        })}
                                        onClick={handleExpandClick}
                                        aria-expanded={expanded}
                                        aria-label="show more"
                                    >
                                        <ExpandMoreIcon/>
                                    </IconButton>
                                </CardActions>
                                <Collapse in={expanded} timeout="auto" unmountOnExit>
                                    <CardContent>


                                        <Typography paragraph>
                                            Участники (
                                            {hikeDescription.numberOfMembers}
                                            ) :
                                            {hikeDescription && hikeDescription.members && hikeDescription.members.map((member, index) =>
                                                <Typography>
                                                    {member.name} {member.surname}
                                                </Typography>
                                            )}
                                        </Typography>
                                    </CardContent>
                                </Collapse>
                            </Card>
                        </Grid>

                    </Grid>
                    :
                    <Grid
                        container
                        direction="row"
                        justify="center"
                        alignItems="flex-start"
                        spacing={5}
                    >
                        <CircularProgress/>
                    </Grid>
            }
        </div>
    );
};
export default HikeDescription;